"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import { v4 as uuidv4 } from "uuid";

type EducationModel = {
  id: string;
  name: string;
  philosophy: string;
  evaluationFocus: string;
  languageFocus: string;
  childFocus: string;
  updatedAt: string;
};

export default function CreateModelPage() {
  const router = useRouter();

  const [models, setModels] = useState<EducationModel[]>([]);
  const [form, setForm] = useState({
    name: "",
    philosophy: "",
    evaluationFocus: "",
    languageFocus: "",
    childFocus: "",
  });
  const [error, setError] = useState("");

  // Êó¢Â≠ò„É¢„Éá„É´„Çí„É≠„Éº„Ç´„É´„Åã„Çâ„É≠„Éº„Éâ
  useEffect(() => {
    const stored = localStorage.getItem("styleModels");
    if (stored) {
      try {
        setModels(JSON.parse(stored));
      } catch {
        setModels([]);
      }
    }
  }, []);

  const handleChange = (field: keyof typeof form, value: string) => {
    setForm((prev) => ({ ...prev, [field]: value }));
  };

  const handleSave = () => {
    setError("");
    if (
      !form.name.trim() ||
      !form.philosophy.trim() ||
      !form.evaluationFocus.trim() ||
      !form.languageFocus.trim() ||
      !form.childFocus.trim()
    ) {
      setError("„Åô„Åπ„Å¶„ÅÆÂøÖÈ†àÈ†ÖÁõÆ„ÇíÂÖ•Âäõ„Åó„Å¶„Åè„Å†„Åï„ÅÑ„ÄÇ");
      return;
    }

    const now = new Date().toISOString();

    const newModel: EducationModel = {
      id: uuidv4(),
      name: form.name.trim(),
      philosophy: form.philosophy.trim(),
      evaluationFocus: form.evaluationFocus.trim(),
      languageFocus: form.languageFocus.trim(),
      childFocus: form.childFocus.trim(),
      updatedAt: now,
    };

    const updatedModels = [newModel, ...models];
    localStorage.setItem("styleModels", JSON.stringify(updatedModels));
    setModels(updatedModels);

    // ‰∏ÄË¶ß„Éö„Éº„Ç∏„Å∏ÈÅ∑Áßª
    router.push("/models");
  };

  return (
    <main style={mainStyle}>
      {/* „Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥ */}
      <nav aria-label="„É°„Ç§„É≥„Éä„Éì„Ç≤„Éº„Ç∑„Éß„É≥" style={navStyle}>
        {[
          ["/", "üè† „Éõ„Éº„É†"],
          ["/plan", "üìã ÊéàÊ•≠‰ΩúÊàê"],
          ["/plan/history", "üìñ Ë®àÁîªÂ±•Ê≠¥"],
          ["/practice/history", "üì∑ ÂÆüË∑µÂ±•Ê≠¥"],
          ["/models/create", "‚úèÔ∏è ÊïôËÇ≤Ë¶≥‰ΩúÊàê"],
          ["/models", "üìö ÊïôËÇ≤Ë¶≥‰∏ÄË¶ß"],
          ["/models/history", "üïí ÊïôËÇ≤Ë¶≥Â±•Ê≠¥"],
        ].map(([href, label]) => (
          <Link key={href} href={href} style={navLinkStyle} aria-current={href === "/models/create" ? "page" : undefined}>
            {label}
          </Link>
        ))}
      </nav>

      <h1 style={titleStyle}>‚úèÔ∏è Êñ∞„Åó„ÅÑÊïôËÇ≤Ë¶≥„É¢„Éá„É´„Çí‰ΩúÊàê</h1>

      {error && (
        <p role="alert" style={errorStyle}>
          {error}
        </p>
      )}

      <form
        onSubmit={(e) => {
          e.preventDefault();
          handleSave();
        }}
        aria-label="ÊïôËÇ≤Ë¶≥„É¢„Éá„É´‰ΩúÊàê„Éï„Ç©„Éº„É†"
        style={formStyle}
      >
        <label htmlFor="modelName" style={labelStyle}>
          „É¢„Éá„É´ÂêçÔºàÂøÖÈ†àÔºâÔºö
        </label>
        <input
          id="modelName"
          type="text"
          value={form.name}
          onChange={(e) => handleChange("name", e.target.value)}
          style={inputStyle}
          required
          autoComplete="off"
        />

        <label htmlFor="philosophy" style={labelStyle}>
          ÊïôËÇ≤Ë¶≥ÔºàÂøÖÈ†àÔºâÔºö
        </label>
        <textarea
          id="philosophy"
          rows={3}
          value={form.philosophy}
          onChange={(e) => handleChange("philosophy", e.target.value)}
          style={textareaStyle}
          required
        />

        <label htmlFor="evaluationFocus" style={labelStyle}>
          Ë©ï‰æ°Ë¶≥ÁÇπ„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
        </label>
        <textarea
          id="evaluationFocus"
          rows={3}
          value={form.evaluationFocus}
          onChange={(e) => handleChange("evaluationFocus", e.target.value)}
          style={textareaStyle}
          required
        />

        <label htmlFor="languageFocus" style={labelStyle}>
          Ë®ÄË™ûÊ¥ªÂãï„ÅÆÈáçË¶ñÁÇπÔºàÂøÖÈ†àÔºâÔºö
        </label>
        <textarea
          id="languageFocus"
          rows={3}
          value={form.languageFocus}
          onChange={(e) => handleChange("languageFocus", e.target.value)}
          style={textareaStyle}
          required
        />

        <label htmlFor="childFocus" style={labelStyle}>
          ËÇ≤„Å¶„Åü„ÅÑÂ≠ê„Å©„ÇÇ„ÅÆÂßøÔºàÂøÖÈ†àÔºâÔºö
        </label>
        <textarea
          id="childFocus"
          rows={3}
          value={form.childFocus}
          onChange={(e) => handleChange("childFocus", e.target.value)}
          style={textareaStyle}
          required
        />

        <button type="submit" style={buttonStyle}>
          ‰ΩúÊàê„Åó„Å¶‰øùÂ≠ò
        </button>
      </form>
    </main>
  );
}

// --- Styles ---

const mainStyle: React.CSSProperties = {
  maxWidth: 1200,
  margin: "0 auto",
  padding: "1rem 2rem",
  fontFamily: "'Segoe UI', Tahoma, Geneva, Verdana, sans-serif",
};

const navStyle: React.CSSProperties = {
  display: "flex",
  gap: 12,
  flexWrap: "wrap",
  marginBottom: 24,
  backgroundColor: "#f0f0f0",
  padding: "1rem",
  borderRadius: 8,
};

const navLinkStyle: React.CSSProperties = {
  backgroundColor: "#1976d2",
  color: "white",
  padding: "0.5rem 1rem",
  borderRadius: 6,
  textDecoration: "none",
  fontWeight: "bold",
  whiteSpace: "nowrap",
  userSelect: "none",
};

const titleStyle: React.CSSProperties = {
  fontSize: "2rem",
  marginBottom: 24,
  textAlign: "center",
};

const errorStyle: React.CSSProperties = {
  color: "#d32f2f",
  fontWeight: "bold",
  marginBottom: 16,
  textAlign: "center",
};

const formStyle: React.CSSProperties = {
  display: "flex",
  flexDirection: "column",
  gap: 16,
  maxWidth: 600,
  margin: "0 auto",
};

const labelStyle: React.CSSProperties = {
  fontWeight: "600",
};

const inputStyle: React.CSSProperties = {
  fontSize: 16,
  padding: "8px 12px",
  borderRadius: 6,
  border: "1px solid #ccc",
};

const textareaStyle: React.CSSProperties = {
  fontSize: 16,
  padding: "8px 12px",
  borderRadius: 6,
  border: "1px solid #ccc",
  resize: "vertical",
};

const buttonStyle: React.CSSProperties = {
  backgroundColor: "#4caf50",
  color: "white",
  fontSize: 18,
  fontWeight: "bold",
  padding: "12px",
  borderRadius: 8,
  border: "none",
  cursor: "pointer",
  userSelect: "none",
};

